*{
    padding: 0;
    margin: 0;
}
:root{
    --ma-bg: #f1eff1;
    --cabin: 'cabin', 'verdana', 'helvetica';
    --border-color: #ddd;
}

body{
    width: 100vw;
    height: 100vh;
    background: var(--ma-bg);
    overflow-x: hidden;
    font-family: var(--cabin);
    font-size: 5px;
}
main{
    display: flex;
}

a{
    text-decoration: none;
    color: inherit;
}
img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
input{
    width: 96%;
    padding: 0 2%;
    border: none;
    background-color: inherit;
}
input, textarea, button :focus{
    outline: none;
}

#content_page{
    width: 100%;
    min-height: 200vh;
    transition: 1s;
}

#SideNav{
    width: 300px;
    height: 100vh;
    position: sticky;
    top: 0px;
    border-right: 3px solid #00000099;
    overflow-y: auto;
    flex-shrink: 0;
}

#sideNavMobile{
    width: 100%;
    padding: 20px 0;
    text-align: center;
    font-size: 4em;
    display: none;
}


@media (max-width: 800px) {
    main{
        flex-direction: column;
    }
    #SideNav{
        position: relative;
        width: 100%;
        height: fit-content;
    }
    #sideNavPC{
        display: none;
    }
    #sideNavMobile{
        display: block;
    }
}