#top_nav{
    width: 96vw;
    height: 50px;
    padding: 5px 2vw;
    box-shadow: 0 1px 1px 1px #00000099;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo{
    font-size: 4.5em;
    font-weight: 600;
}

.links{
    display: flex;
    height: 100%;
}

.links a{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 25px;
    font-size: 3.5em;
    font-weight: 600;
}
.links a:hover{
    border-bottom: 2px #000 solid;
}
